import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Post } from '../classes/post';

@Injectable()

export class freeApiService{

    readonly CON_URL = "https://jsonplaceholder.typicode.com/comments";

    readonly CON_URLPOST = "https://jsonplaceholder.typicode.com/posts";

    constructor (private httpClient: HttpClient){}

    getComments(): Observable<any>{
        return this.httpClient.get(this.CON_URL);
    }

    getCommentByParam(): Observable<any>{

        let param1 = new HttpParams().set("userId", "1");
        return this.httpClient.get(this.CON_URLPOST, { params: param1 });
        
    }

    post(newPost: Post): Observable<any>{
        return this.httpClient.post(this.CON_URLPOST, newPost);
    }

}