import { Component } from '@angular/core';

import { freeApiService } from '../app/services/freeapi.service';
import { Comments } from './classes/comments';
import { Post } from './classes/post';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css' ]
})
export class AppComponent {

  title = 'App';
  
  constructor(private _freeApiService: freeApiService){
    
  }
  
  listComments: Comments[];
  listPosts: Post[];
  objPosts: Post;


  ngOnInit() {
    
    //Get data from server
    this._freeApiService.getComments()
      .subscribe(
        data=>{
          this.listComments = data;
         
        }
    );
    
    //Get data from server with parameter
    this._freeApiService.getCommentByParam()
      .subscribe(
        data => {
          this.listPosts = data
        }
    );
    

    //Insert data from server
    let newPost = new Post();

    newPost.body = 'lorem ipsumwakak';
    newPost.title = 'gago';
    newPost.id = 777;

    this._freeApiService.post(newPost)
      .subscribe(
        data => {
          this.objPosts = data;
        }
      );

  }

}
