
import { emailService } from './../services/email.service';
import { Email } from './../classes/email';

import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  form: FormGroup;
  objEmail: Email;

  constructor(public fb: FormBuilder, private _emailService : emailService) {
    this.form = this.fb.group({
      name: [''],
      emailAdd: [''],
      subject: [''],
      message:['']
      
    })
  }


  ngOnInit(): void { }

  resetForm() {
    this.form.reset();
  }
  

  sendEmail() {

    var objEmail = new Email();

    objEmail.name = this.form.get("name")?.value;
    objEmail.email = this.form.get("emailAdd")?.value;
    
    objEmail.subject = this.form.get("subject")?.value;
    objEmail.message = this.form.get("message")?.value;

    
    this._emailService.post(objEmail)
      .subscribe(
        (response) => console.log(response),
        (error) => console.log(error)
      )
  }
}

